.productdisplay {
    display: flex;
    margin: 0px 170px;
}
.productdisplay-left {
    display: flex;
    gap: 17px;
}
.productdisplay-img-list {
    display: flex;
    flex-direction: column;
    gap: 16px;
}
.productdisplay-img-list img {
    height: 163px;
}
.productdisplay-main-img{
    width: 586px;
    height: 700px;
}
.productdisplay-right {
    margin: 0px 70px;
    display: flex;
    flex-direction: column;
}
.productdisplay-right h1 {
    color: #3d3d3d;
    font-size: 40px;
    font-weight: 700;
}
.productdisplay-right-stars{
    display: flex;
    align-items: center;
    margin-top: 13px;
    gap: 5px;
    color: #1c1c1c;
    font-size: 16px;
}
.productdisplay-right-prices {
 
    margin: 40px 0px;
    gap: 30px;
    font-size: 24px;
    font-weight: 700;

}
.productdisplay-right-prices-old {
    color: #818181;
    text-decoration: line-through;
}
.productdisplay-right-prices-old{
    color: #ff4141;
}
.productdisplay-right-size h1 {
    margin-top: 55px;
    color: #656565;
    font-size: 20px;
    font-weight: 600;
}
.productdisplay-right-sizes {
   display: flex;
    margin: 30px 0px;
    gap: 20px;
}